import Box from '@mui/material/Box'
import { AxisBottom } from '@visx/axis'
import { Group } from '@visx/group'
import { scaleBand, scaleLinear } from '@visx/scale'
import { Bar } from '@visx/shape'
import dayjs from 'dayjs'
import { useEffect, useState } from 'react'
import { useGustavoStore } from 'views/gustavo'

export const SummaryByDate = () => {
    const { filteredSpendData: spendData } = useGustavoStore()

    const [totalSpendByDate, setTotalSpendByDate] = useState(new Map<string, number>())

    useEffect(() => {
        const totalSpendByDate = spendData.reduce((totalSpend, spend) => {
            const currentDate = dayjs(spend.date).format('M/D')
            const total = totalSpend.get(currentDate) || 0
            totalSpend.set(currentDate, total + spend.convertedCost)
            return totalSpend
        }, new Map<string, number>())
        setTotalSpendByDate(totalSpendByDate)
    }, [spendData])

    const renderGraph = () => {
        const width = window.innerWidth * 0.9
        const height = width
        const marginY = 12

        const sortedDates = Array.from(totalSpendByDate.keys())
            .map((date) => dayjs(date))
            .sort((a, b) => a.diff(b))

        const dateRange: dayjs.Dayjs[] = []
        const earliestDate = sortedDates[0]
        const latestDate = sortedDates[sortedDates.length - 1]
        let currentDate = earliestDate
        while (currentDate.isBefore(latestDate, 'day') || currentDate.isSame(latestDate, 'day')) {
            dateRange.push(currentDate)
            currentDate = currentDate.add(1, 'day')
        }
        const dateRangeStrings = dateRange.map((date) => date.format('M/D'))

        const dateScale = scaleBand<string>({
            range: [0, width],
            domain: dateRangeStrings,
            padding: 0.2,
        })
        const spendScale = scaleLinear<number>({
            range: [height, 0],
            domain: [0, Math.max(...Array.from(totalSpendByDate.values()))],
        })

        return (
            <Box
                sx={{
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    padding: 2,
                    margin: 1,
                    border: '1px solid #FBBC04',
                    borderRadius: '10%',
                    backgroundColor: 'white',
                }}>
                <svg width={width} height={height + marginY}>
                    <rect width={width} height={height + marginY} fill="none" rx={14} />
                    <Group>
                        {sortedDates
                            .map((date) => date.format('M/D'))
                            .map((date, i) => {
                                const totalSpend = totalSpendByDate.get(date) || 0

                                const barWidth = dateScale.bandwidth()
                                const barHeight = height - (spendScale(totalSpend) ?? 0)
                                const barX = dateScale(date)
                                const barY = height - barHeight - marginY

                                return (
                                    <Bar
                                        key={i}
                                        x={barX}
                                        y={barY}
                                        width={barWidth}
                                        height={barHeight}
                                        fill={'#f4d35e'}
                                    />
                                )
                            })}
                    </Group>
                    <AxisBottom top={height - marginY} scale={dateScale} />
                </svg>
            </Box>
        )
    }

    return <Box>{totalSpendByDate.size && renderGraph()}</Box>
}
